@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-dm-sans: "DM Sans";
  --font-inter: Inter;

  /* font sizes */
  --font-size-sm: 14px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-xs: 12px;
  --font-size-mini: 15px;
  --font-size-smi: 13px;
  --font-size-3xs: 10px;
  --font-size-xl: 20px;
  --font-size-5xs: 8px;
  --font-size-13xl: 32px;

  /* Colors */
  --text-inverse: #fff;
  --color-dimgray-100: #55585f;
  --color-dimgray-200: #55585e;
  --color-dimgray-300: #54575e;
  --color-darkgray: #979797;
  --color-gray: #231f20;
  --color-lightblue: #b5dcf2;
  --color-plum-100: #c7b5f3;
  --color-plum-200: #c6b5f2;
  --color-whitesmoke-100: #f6f6f6;
  --color-black: #000;
  --color-paleturquoise: #b6f0e4;
  --color-lightseagreen: #29b0a7;
  --color-slateblue: #2c42c2;
  --color-silver: #c4c4c4;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-7xs: 6px;
  --gap-2xs: 11px;
  --gap-9xs: 4px;
  --gap-4xl: 23px;
  --gap-142xl: 161px;
  --gap-xl: 20px;
  --gap-lgi: 19px;

  /* Paddings */
  --padding-6xl: 25px;
  --padding-9xs: 4px;
  --padding-21xl: 40px;
  --padding-12xs: 1px;
  --padding-18xl: 37px;
  --padding-7xl: 26px;
  --padding-20xl: 39px;
  --padding-mid: 17px;
  --padding-10xs: 3px;
  --padding-2xs: 11px;
  --padding-xl: 20px;
  --padding-7xs: 6px;

  /* Border radiuses */
  --br-87xl: 106px;
  --br-3xs: 10px;
  --br-mini: 15px;
  --br-481xl: 500px;
  --br-5xs: 8px;
  --br-6xl: 25px;
}
