.label {
  position: relative;
  letter-spacing: -0.02em;
  line-height: 24px;
  display: inline-block;
  width: 292px;
  height: 23px;
  flex-shrink: 0;
}
.vectorIcon {
  position: relative;
  width: 33px;
  height: 35px;
  z-index: 0;
}
.vectorIcon1 {
  position: absolute;
  margin: 0 !important;
  height: 81.25%;
  width: 63.22%;
  top: 10.42%;
  right: 18.39%;
  bottom: 8.33%;
  left: 18.39%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.notificationsNone {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-3xs);
}
.vectorIcon2 {
  position: relative;
  width: 28.13px;
  height: 28.13px;
}
.materialSymbolssearch {
  display: flex;
  flex-direction: row;
  padding: var(--padding-7xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.avatarIcon {
  position: relative;
  width: 45.85px;
  height: 46.89px;
  object-fit: cover;
}
.notificationsNoneParent {
  height: 46.89px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-lgi);
}
.navbar {
  align-self: stretch;
  background-color: var(--text-inverse);
  border-bottom: 1px solid var(--color-silver);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: var(--padding-2xs) var(--padding-xl);
  align-items: flex-start;
  justify-content: space-between;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-dm-sans);
}
