.label {
  align-self: stretch;
  position: relative;
  letter-spacing: -0.02em;
  line-height: 24px;
}
.premierPlusBnk {
  margin: 0;
}
.label1 {
  top: 11px;
  left: 15px;
  color: var(--color-slateblue);
  display: inline-block;
  width: 190px;
  height: 23px;
}
.label1,
.label2,
.label3 {
  position: absolute;
  letter-spacing: -0.02em;
  line-height: 24px;
  font-weight: 500;
}
.label2 {
  top: 59px;
  left: 28px;
  font-size: var(--font-size-3xs);
}
.label3 {
  top: 0;
  right: 0;
}
.label4,
.labelContainer {
  position: absolute;
  width: 96px;
}
.label4 {
  top: 16px;
  right: 0;
  letter-spacing: -0.02em;
  line-height: 24px;
  font-weight: 500;
  display: inline-block;
  height: 19px;
}
.labelContainer {
  top: 39px;
  right: 395px;
  height: 35px;
}
.label5,
.label6 {
  position: absolute;
  letter-spacing: -0.02em;
  line-height: 24px;
}
.label5 {
  top: 39px;
  left: 15px;
  font-size: var(--font-size-5xl);
}
.label6 {
  top: 1px;
  right: 104px;
  font-weight: 500;
}
.groupChild {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: var(--br-5xs);
  background: linear-gradient(rgba(217, 217, 217, 0), rgba(217, 217, 217, 0)),
    0 0;
  border: 1px solid var(--color-slateblue);
  box-sizing: border-box;
  width: 67.5px;
  height: 27px;
}
.label7 {
  position: absolute;
  top: 1px;
  right: 17px;
  letter-spacing: -0.02em;
  line-height: 24px;
  font-weight: 500;
}
.rectangleParent {
  top: 0;
  right: 0;
  width: 67.5px;
}
.groupDiv,
.groupItem,
.rectangleParent {
  position: absolute;
  height: 27px;
}
.groupItem {
  top: 0;
  right: 74.25px;
  border-radius: var(--br-5xs);
  background: linear-gradient(rgba(217, 217, 217, 0), rgba(217, 217, 217, 0)),
    0 0;
  border: 1px solid var(--color-slateblue);
  box-sizing: border-box;
  width: 159.75px;
}
.groupDiv {
  top: 9px;
  right: 55px;
  width: 234px;
  font-size: var(--font-size-sm);
  color: var(--color-slateblue);
}
.labelGroup {
  align-self: stretch;
  flex: 1;
  position: relative;
  border: 1px solid var(--color-slateblue);
  font-size: var(--font-size-smi);
}
.labelParent {
  align-self: stretch;
  height: 116px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-mini);
  color: var(--color-gray);
  font-family: var(--font-dm-sans);
}
