.pendingTransactions {
  position: relative;
  letter-spacing: -0.03em;
  line-height: 100%;
}
.pendingTransactionsWrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-6xl);
  align-items: flex-start;
  justify-content: flex-start;
}
.imageChild {
  position: relative;
  border-radius: var(--br-87xl);
  background-color: var(--color-lightblue);
  width: 37px;
  height: 35px;
  z-index: 0;
}
.ionfastFoodIcon {
  position: absolute;
  margin: 0 !important;
  top: calc(50% - 9.5px);
  left: calc(50% - 10.5px);
  width: 20px;
  height: 19px;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 1;
}
.image {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-3xs);
}
.cafeAndRestaurant {
  position: relative;
  font-size: var(--font-size-sm);
  letter-spacing: -0.03em;
  line-height: 100%;
  color: var(--color-darkgray);
}
.orderFromCentralBurgerParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.amount18936Parent,
.imageParent {
  position: absolute;
  display: flex;
  justify-content: space-between;
}
.imageParent {
  top: 20px;
  left: 24px;
  width: 243px;
  flex-direction: row;
  align-items: center;
}
.amount18936Parent {
  top: 18px;
  right: 20px;
  height: 38px;
  flex-direction: column;
  align-items: flex-end;
  text-align: center;
}
.frameGroup {
  align-self: stretch;
  position: relative;
  border-bottom: 1px solid var(--color-dimgray-300);
  box-sizing: border-box;
  height: 74px;
  overflow: hidden;
  flex-shrink: 0;
  font-size: var(--font-size-base);
  color: var(--color-gray);
}
.frameParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-7xs);
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-darkgray);
  font-family: var(--font-dm-sans);
}
